<template>
  <div ref="chart" :style="{width: width, height: height}"></div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '220px'
    }
  },
  mounted () {
    this.drawChart()
  },
  methods: {
    drawChart () {
      const chartEl = this.$echarts.init(this.$refs.chart)

      chartEl.setOption({
        tooltip: {
          trigger: 'axis'
        },
        legend: {
          data: ['UV 产出', '跳失率']
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        toolbox: {
          feature: {
            saveAsImage: {}
          }
        },
        xAxis: {
          type: 'category',
          data: ['用户1', '用户2', '用户3', '用户4', '用户5', '用户6', '用户7']
        },
        yAxis: [
          {
            type: 'value',
            scale: true,
            name: 'UV 产出',
            max: 500,
            min: 0,
            boundaryGap: [0, 500]
          },
          {
            type: 'value',
            scale: true,
            name: '跳失率',
            max: 100,
            min: 0,
            boundaryGap: [0, 100]
          }
        ],
        series: [
          {
            name: 'UV 产出',
            type: 'bar',
            stack: '总量',
            data: [120, 132, 101, 134, 90, 230, 210]
          },
          {
            name: '跳失率',
            type: 'line',
            stack: '总量',
            data: [20, 34, 56, 78, 100, 36, 56]
          }
        ]
      })
    }
  }
}
</script>

