<template>
  <div ref="chart" :style="{width: '400px', height: '300px'}"></div>
</template>

<script>
export default {
  props: {
    seriesRadius: {
      type: [String, Array],
      default: '55%'
    },
    data: {
      type: Array
    }
  },
  mounted () {
    this.drawChart()
  },
  methods: {
    drawChart () {
      const chartEl = this.$echarts.init(this.$refs.chart)

      chartEl.setOption({
        tooltip: {
          trigger: 'item',
          formatter: '{a} <br/>{b} : {c} ({d}%)'
        },
        legend: {
          orient: 'horizontal',
          top: 'top'
        },
        series: [
          {
            type: 'pie',
            radius: this.seriesRadius,
            center: ['50%', '60%'],
            data: this.data,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 0,
                shadowColor: 'rgba(0, 0, 0, 0.5)'
              }
            }
          }
        ]
      })
    }
  }
}
</script>

