<template>
  <div ref="chart" :style="{width: width, height: height}"></div>
</template>

<script>
export default {
  props: {
    width: {
      type: String,
      default: '100%'
    },
    height: {
      type: String,
      default: '220px'
    },
    chartData: {
      type: Object
    },
    xAxisLabel: {
      type: Object
    }
  },
  mounted () {
    this.drawChart()
  },
  methods: {
    drawChart () {
      const chartEl = this.$echarts.init(this.$refs.chart)

      chartEl.setOption({
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'cross',
            label: {
              backgroundColor: '#6a7985'
            }
          }
        },
        legend: {
          // data: ['邮件营销', '联盟广告']
        },
        toolbox: {
        },
        grid: {
          left: '3%',
          right: '4%',
          bottom: '3%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            boundaryGap: false,
            data: this.chartData.xAxisData,
            axisLabel: {
              interval: this.xAxisLabel && this.xAxisLabel.interval ? this.xAxisLabel.interval : 'auto'
            }
          }
        ],
        yAxis: [
          {
            type: 'value'
          }
        ],
        series: this.chartData.data
      })
    }
  }
}
</script>

